@import url(https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap);
:root{
  --primaryDark-color: #2E3532;
  --primaryLight-color: #E0E2DB;
  --light-color: #D2D4C8;
  --red-color: #8B2635;

  --font-family: 'JetBrains Mono', sans-serif;
}


html, body, *, *:after, *:before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'JetBrains Mono', sans-serif;
  font-family: var(--font-family);
}

#root{
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

header{
    padding-top: 30px;
    display: flex;
    justify-content: space-around;
    align-items: baseline;
}


nav>ul{
    list-style-type: none;
    display: inline-flex;
    grid-gap: 60px;
    gap: 60px;
}

.logo {
    width: clamp(150px, 15vw, 25vw);
    aspect-ratio: 818/133;
    background-image: url(/static/media/logoDark.6d0a9e94.gif);
    background-size: cover;
}


.main{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.main>.hero{
    height: 100vh;
    background-image: url(/static/media/belem.116b13c9.mp4);
}
